.NavigateBathComponent {
  width: 100%;
  display: grid;
  grid-gap: 5px;
  color: #E5DCCB !important;
  .NavigateBathComponent_button {
    display: grid;
    height: auto;
    width: 100%;
    align-items: center;
    justify-items: center;
    text-align: center;
    background: #847C6E;
    padding: 5px;
    min-height: 45px;
    transition: background-color,color 1s revert;
    &:visited {
      //transition: background-color,color 0.5s ease-in-out;
    }
  }
}

.animateClass {
  animation: animateChangeColor 0.5s ease-in-out;
}

@keyframes animateChangeColor {
  0% {
    background: #847C6E;
    color: #E5DCCB
  }
  50% {
    background: #E5DCCB;
    color: #847C6E;
  }
  100% {
    background: #847C6E;
    color: #E5DCCB
  }
}

