@media screen and (max-width: 480px) {
  .main-page {
    max-width: 100% !important;
    min-width: auto;
    width: 100% !important;
  }
  .calculator-open {
    max-width: 100% !important;
  }
  .calculator_roomSize {
    grid-template-columns: 1fr !important;
    grid-column: 1 !important;
  }
  .header {
    max-width: 100% !important;
    min-width: auto !important;
  }
  .calculator_tileSize {
    max-width: 100% !important;
    min-width: auto !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 100px repeat(8, 250px);
    width: 100% !important;
    margin: 0 !important;
  }
  .calculator_tileSize h2 {
    min-width: auto !important;
    max-width: 100%;
    grid-column: 1 !important;
  }
  .calculator_tileSize .calculator_tileSize__items {
    width: 80% !important;
    grid-column: 1 !important;
    height: 250px !important;
    grid-row: auto !important;
  }
  .calculatorBathMetres {
    width: 100% !important;
    grid-template-columns: 1fr !important;
    height: auto !important;
    min-width: auto !important;
    margin: 0 !important;
    max-width: 410px !important;
  }
  .calculatorBathMetres img {
    width: 80%;
    padding: 25px;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic {
    grid-gap: 5px;
    padding-bottom: 50px !important;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic .calculatorBathMetres-mainPic_inputField {
    width: 90% !important;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic h2 {
    padding-top: 0 !important;
    font-size: 40px !important;
    max-width: 700px !important;
  }
  .calculatorBath-additionalWork_opened__items {
    min-width: auto !important;
    max-width: 100% !important;
  }
  .AdditionalItemAngle {
    row-gap: 15px !important;
  }
  .footer {
    width: 100% !important;
  }
  .ListOfBenefitAndLimit {
    display: flex !important;
    flex-direction: column;
    gap: 15px;
  }
  .ListOfBenefitAndLimit-group {
    margin-top: 15px;
  }
  .ModalContainer {
    overflow-x: hidden !important;
    min-width: auto !important;
    max-width: 100% !important;
  }
  .AdditionalItemAngle_degree, .AdditionalItemAngle_profile {
    grid-gap: 10px;
  }
  .ModalTable {
    min-width: auto !important;
    max-width: 100% !important;
  }
  .ModalTable .ModalTable_Row__items {
    padding: 6px !important;
  }
}

