.ButtonModalList {
  font-weight: 500;
  display: grid;
  align-self: end;
  height: 100%;
  width: 100%;
  font-size: 15px;
  font-family: "Playfair Display", serif;
  align-items: center;
  justify-content: end;
  letter-spacing: 2.5px;
  color: #E5DCCB;
}
.ButtonModalList .ButtonModalList_icon {
  margin-right: 25px;
  position: relative;
  width: 50px;
  height: 50px;
  border: 1px solid #847C6E;
}
.ButtonModalList .ButtonModalList_icon .ButtonModalList_icon__span {
  width: 15px;
  position: absolute;
  right: 8px;
  display: inline-block;
  line-height: 13px;
  top: 8px;
  height: 15px;
  color: #fff;
  border-radius: 50%;
  background: red;
}

.ModalContainer {
  margin: 15px auto;
  z-index: 10;
  max-width: 1280px;
  min-width: 650px;
  max-height: 90%;
  background: #847C6E;
  color: #E5DCCB;
  overflow-y: scroll;
}

.ModalTable {
  margin: auto;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: #847C6E;
  color: #E5DCCB;
}
.ModalTable .ModalTable_Row__items {
  color: #E5DCCB;
}

.ModalDeleteIcon {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  color: #E5DCCB !important;
}

